import TextField, { } from "@mui/material/TextField"
import * as React from "react"


export function BoundNumberField({
  state,
  prop,
  setStatePartial,
  ...props
}



) {
  return (
    React.createElement(TextField, {
      variant: "outlined",
      size: "small",
      type: "number",
      value: state[prop],
      onChange: (e) =>
        setStatePartial({ [prop]: +e.target.value } )
      ,
      label: prop,
      ...props,}
    )
  )
}
